* {
  outline: 0;
  padding: 0;
  margin: 0;
  border: 0;
}
:root {
  --left-offset: 20%;
  --left-offset-welcome: 18%;
}
nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 6vh;
  color: #fff;
  background: tomato;
}
a {
  text-decoration: none;
  color: #3716a9;
}

body {
  background: #fff;
  font-family: "Droid Sans Mono";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
}

.home-mobile {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: auto;
  overflow: hidden;
}
.home {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: scroll;
  perspective: 8px;
  perspective-origin: 0;
}
.fruits {
  position: absolute;
  width: 74px;
  height: 24px;
  left: 18%;
  left: var(--left-offset-welcome);
  top: 38px;
  font-size: 24px;
  line-height: 24px;
  display: flex;
  align-items: flex-end;
  letter-spacing: -1px;
  color: #3716a9;
}
.fruits:hover {
  cursor: url(data:image/png;base64,),
    auto;
}
.title {
  height: 114px;
  left: 0;
  top: 0;
  font-size: 28px;
  line-height: 34px;
  display: flex;
  align-items: flex-end;
  letter-spacing: -0.65625px;
  color: #000;
}
.title,
.welcome-text {
  position: absolute;
  width: 390px;
}
.welcome-text {
  height: 259px;
  left: 18%;
  left: var(--left-offset-welcome);
  top: 212px;
}
.welcome-text p {
  height: 110px;
  left: 0;
  top: 149px;
  color: #000;
}
.welcome-text a,
.welcome-text p {
  position: absolute;
  width: 380px;
  display: flex;
  align-items: center;
}
.welcome-text a {
  height: 50px;
  top: 90px;
}
.blue-box {
  width: 10%;
  height: 486px;
  left: 0;
  top: 701px;
  transform-origin: 0 0;
  transform: translateZ(-3px) scale(1.375);
  background: #a0d8f7;
}
.blue-box,
.img-1 {
  position: absolute;
}
.img-1 {
  height: 791px;
  max-height: 791px;
  width: 100vw;
  left: 9.44%;
  top: 671px;
  overflow-y: hidden;
  background-color: #f9bac5;
}
.img-1 img {
  position: absolute;
  left: calc(10.56% - 150px);
  left: calc(var(--left-offset) - 150px - 9.44%);
  z-index: 1;
}
.current-work {
  position: absolute;
  width: 380px;
  height: 132px;
  left: 20%;
  left: var(--left-offset);
  top: 871px;
  display: flex;
  align-items: center;
  z-index: 2;
}
.img-2 {
  position: absolute;
  width: 100%;
  height: 791px;
  left: 0;
  top: 1462px;
  background-color: #a0d8f7;
}
.img-2 img {
  position: absolute;
  left: calc(20% - 255px);
  left: calc(var(--left-offset) - 255px);
}
.pink-box {
  position: relative;
  width: 10vw;
  min-width: 233px;
  height: 1080px;
  top: -222px;
  left: 33vw;
  float: right;
  z-index: 2;
  background: #f9bac5;
  transform-origin: 0 69%;
  transform: translateZ(-3px) scale(1.375);
}
.yellow-box {
  width: 90%;
  min-width: 1150px;
}
.img-3,
.yellow-box {
  position: absolute;
  height: 791px;
  top: 2253px;
  background-color: #f4c76c;
}
.img-3 {
  left: calc(20% - 283px);
  left: calc(var(--left-offset) - 283px);
  overflow-y: hidden;
}
.footer-desktop {
  position: relative;
  display: flex;
  width: 380px;
  height: 310px;
  left: 20%;
  left: var(--left-offset);
  top: 3294px;
  margin-bottom: 200px;
}
.footer-text {
  width: 380px;
  height: 66px;
  top: 0;
}
.contact-us,
.footer-text {
  position: absolute;
  left: 0;
  display: flex;
  align-items: center;
}
.contact-us {
  width: 85px;
  height: 22px;
  top: 88px;
}
.slash {
  width: 8px;
  left: 110px;
  color: #bebebe;
}
.see-fruits,
.slash {
  position: absolute;
  height: 22px;
  top: 88px;
  display: flex;
  align-items: center;
}
.see-fruits {
  width: 85px;
  left: 144px;
}
.see-fruits:hover {
  cursor: url(data:image/png;base64,),
    auto;
}

/* fruit-gallery */
.fruit-gallery {
  align-items: center;
  overflow: hidden;
}
.fruit-big-pic,
.fruit-gallery {
  display: flex;
  flex-direction: column;
}
.fruit-big-pic,
.signature {
  width: 100vw;
}
.signature {
  position: relative;
  height: 17vh;
  left: 20vw;
  top: 4vh;
  color: #191919;
}

/* mobile below */
.section-1-mobile {
  display: flex;
  width: 100vw;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 130vh;
}
.fruits-link-mobile {
  height: 24px;
  width: 85%;
  max-width: 354px;
  margin-top: 38px;
  font-size: 24px;
  line-height: 24px;
  display: flex;
  align-items: flex-start;
  letter-spacing: -1px;
}
.welcome-text-mobile {
  display: flex;
  width: 85%;
  max-width: 354px;
  flex-direction: column;
  justify-content: space-between;
}
.title-mobile {
  display: flex;
  justify-content: flex-start;
  font-size: 24px;
  line-height: 24px;
  align-items: flex-end;
  letter-spacing: -0.56625px;
}
.welcome-text-mobile p {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5vh;
  margin-bottom: 5vh;
}
.welcome-text-mobile a {
  width: 100%;
  height: 50px;
}
.pink-box-mobile {
  display: flex;
  width: 100%;
  align-items: flex-end;
  justify-content: center;
  background-color: #f9bac5;
  height: 40vh;
}
.img-mobile {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.pink-background {
  background-color: #f9bac5;
}
.blue-background {
  background-color: #a0d8f7;
}
.yellow-background {
  background-color: #f4c76c;
}

.text-section-mobile {
  display: flex;
  width: 85%;
  max-width: 354px;
  height: auto;
}
.footer-mobile {
  align-items: center;
  height: 50vh;
}
.footer-mobile {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.footer-text-mobile {
  width: 85%;
  max-width: 354px;
  margin-bottom: 5vh;
}

.footer-links-mobile {
  display: flex;
  width: 85%;
  justify-content: flex-start;
}
